/* eslint-disable no-unused-vars */
import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import axios from 'axios';
import { Link } from 'react-router-dom';
import AppNavbar from '../../../components/AppNavbar';
import FooterNavbar from '../../../components/FooterNavbar';
import { TabContent, TabPane, Nav, NavItem, NavLink, Card, Button, ButtonGroup, CardTitle, CardText, Row, Col } from 'reactstrap';
import classnames from 'classnames';
class Create extends Component {

  constructor() {
    super();
    this.state = {
      activeTab: '1',
      logo: '',
      nameClub: '',
      cetagoryClub: '',
      username: '',
      password: '',
      status: '',
    };
    this.toggle = this.toggle.bind(this);
  }

  toggle(tab) {
    if (this.state.activeTab !== tab) {
      this.setState({
        activeTab: tab
      });
    }
  }

  /* onChange = (e) => {
    const state = this.state
    state[e.target.name] = e.target.value;
    this.setState(state);
  } */

  onSubmit = (e) => {
    e.preventDefault();

    const { logo, nameClub, cetagoryClub, username, password, status } = this.state;

    axios.post('/contacts', { logo, nameClub, cetagoryClub, username, password, status })
      .then((result) => {
        this.props.history.push("/")
      });
  }

  render() {
    const { logo, nameClub, cetagoryClub, username, password, status } = this.state;
    return (
      <div>
        <AppNavbar />
        <div class="container-fluid">
          <div class="Row container-layer2">
            <div class="Row px-5 pt-5">
              <h1 align="center">CLUB</h1>
              <Nav tabs>
                <NavItem>
                  <NavLink
                    className={classnames({ active: this.state.activeTab === '1' })}
                    onClick={() => { this.toggle('1'); }}
                  >Club
            </NavLink>
                </NavItem>
                <NavItem>
                  <NavLink
                    className={classnames({ active: this.state.activeTab === '2' })}
                    onClick={() => { this.toggle('2'); }}
                  >News
            </NavLink>
                </NavItem>
              </Nav>
              <div class="Row container-layer3">
              </div>
            </div>
          </div>
          <FooterNavbar />
        </div>
      </div>
    );
  }
}

export default Create;